.modal-delete-folder {
  width: 385px;
  height: 180px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  // position: relative;
  // top: -200px;
  .modal__title {
    margin: 0;
    font-size: 20px;
    margin: 4px 0 18px 2px;
    color: #3e3f44;
  }

  .text {
    font-size: 12px;
    color: #828292;
    font-weight: 500;
    line-height: 1.67;
    letter-spacing: 0.2px;
  }

  .modal__footer {
    display: flex;
    justify-content: flex-start;
    margin-top: 10px;
    

    .no {

      width: 97px;
      height: 40px;
      border-radius: 5px;
      border: solid 2px $primary;
      color: $primary;
      background-color: #fff;
    }
    .yes {

      margin-right: 25px;
      width: 97px;
      height: 40px;
      border-radius: 5px;
      background-color: $primary;
    }
  }
}
