.btn {
  padding: 8px 20px;
  border-radius: 5px;
  font-size: 16px;
  line-height: 20px;
  font-family: "Inter Bold";
  white-space: pre;

  &--primary {
    color: #fff;
    background: $primary;
    border: 1px solid $primary;
    position: relative;
    z-index: 2;
  }

  &--light {
    color: $light-2;
    background: #fff;
    border: 1px solid $light-2;
  }


  @include clickable();
  
}

.btn-outlined {
  color: $light-2;
  border-radius: 5px;
  margin-right: 14px;
  font-size: 14px;
  padding: 5px 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid $light-2;
  background: transparent;

  &--primary {
    color: $primary;
    border: 1px solid $primary;
  }

  @include clickable();
}

.btn-text {
  font-size: 16px;
  line-height: 20px;
  border: none;
  background: transparent;
  color: $light-2;
  font-family: "Inter Regular";
  white-space: pre;

  @include clickable();

  &--underline {
    text-decoration: underline;
  }
}

.btn-group {
  position: relative;
  display: flex;
  background: $light-2;
  width: 255px;
  border-radius: 50px;

  &__mask {
    background: #fff;
    border: 1px solid $primary;
    border-radius: 50px;
    height: 100%;
    width: 50%;
    left: 0%;
    position: absolute;
    transition: 0.2s;
    transition-delay: 0.05s;
  }

  &__btn {
    padding: 4px 15px;
    border-radius: 50px;
    font-size: 16px;
    line-height: 20px;
    width: 50%;
    border: none;
    background: transparent;
    color: #fff;

    &__left {
      color: $primary;
      font-family: "Inter Bold";
      z-index: 2;
    }

    &__right {
    }

    @include clickable();
  }

  &--block {
    width: 100%;
  }

  &.toggle {
    & .btn-group__mask {
      left: 50%;
    }
    & .btn-group__btn__left {
      font-family: "Inter Regular";
      color: #fff;
    }
    & .btn-group__btn__right {
      color: $primary;
      z-index: 2;
      font-family: "Inter Bold";
    }
  }
}

.btn-normal-group {
  display: flex;

  &__item {
    color: $light-2;
    border-radius: 5px;
    margin-right: 14px;
    font-size: 14px;
    padding: 5px 15px;
    width: 94px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid $light-2;
    background: transparent;
    text-transform: uppercase;
    cursor: pointer;

    &:hover {
      border: 1px solid $primary;
      opacity: 0.8;
      color: $primary;
    }

    &.active {
      border: 1px solid $primary;
      color: $primary;
      font-family: "Inter Bold";
    }
  }
}

.btn-overlay {
  background: $primary;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 52px;
  height: 52px;
  border-radius: 50%;
  border: none;
  cursor: pointer;
  transition: 0.18s;
  z-index: 1;

  &__chat-icon {
    margin-top: 8px;
    svg {
      path,
      rect {
        fill: #fff;
      }
    }
  }

  &__close-icon {
    display: none;

    .material-icons {
      color: #fff;
      font-size: 28px;
    }
  }

  &:hover {
    background: $primary-3;
  }

  &:active {
    background: $primary;
  }

  &.active {
    .btn-overlay__close-icon {
      display: flex;
    }
    .btn-overlay__chat-icon {
      display: none;
    }
  }
}

.channel-group {
  display: flex;
  margin-bottom: 24.5px;
  height: 169px;
  width: 100%;

  &__item {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 144px;
    padding: 10px;
    border: 1px solid $light-2;
    border-left: none;
    position: relative;
    transition: 0.15s;
    cursor: pointer;

    &__check {
      right: 56px;
      bottom: 10px;
      position: absolute;
      color: $primary;
      display: none;

      .material-icons {
        font-size: 15px;
      }
    }

    &__media {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin: 36px 0;

      &__img {
      }

      &__title {
        font-size: 16px;
        line-height: 20px;
        margin-top: 14px;
        color: $light-2;
      }
    }

    &.active {

      background: $light-4;

      .channel-group__item__media__title {
        font-family: "Inter Semibold";
        color: $primary;
      }
      .channel-group__item__check {
        display: flex;
      }
    }

    &:active {
      opacity: 0.8;
    }

    &:hover {
      background: $light;
    }

    &:first-child {
      border-radius: 5px 0 0 5px;
      border-left: 1px solid $light-2;
    }

    &:last-child {
      border-radius: 0 5px 5px 0;
    }
  }
}

.radio-content {
  display: flex;

  &--modal {
    flex-direction: column;
    margin-bottom: 10px;
    padding: 5px;

    .radio-button {
      margin-bottom: 10px;
  
      &:last-child {
        margin-bottom: 0;
      }
    }
  
    input[type="radio"] {
      display: none;
  
      + label {
        display: flex;
        cursor: pointer;
        align-items: center;
        .text {
          margin-left: 8px;
          font-size: 16px;
          line-height: 20px;
          color: $dark-3;
        }
        span {
          min-width: 15px;
          min-height: 15px;
          // margin: -1px 4px 0 0;
          cursor: pointer;
          border-radius: 50px;
          border: 1px solid $light-2;
          display: flex;
          align-items: center;
          justify-content: center;
          padding: 0;
  
          .mask {
            // opacity: .2;
            transition: all 0.3s ease;
            min-height: 7px;
            min-width: 7px;
            border-radius: 50px;
            background: $light-2;
          }
        }
      }
  
      &:checked + label span {
        border: 1px solid $dark-2;
        .mask {
          background: $dark-2;
        }
      }
    }
  }

  &--column {
    flex-direction: column;
  }

  &--row {
    flex-direction: row;

    & .margin {
      margin-right: 30px;
    }
  }

  .radio-button {
    margin-bottom: 10px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  input[type="radio"] {
    display: none;

    + label {
      display: flex;
      cursor: pointer;
      align-items: center;
      .text {
        margin-left: 8px;
        font-size: 16px;
        line-height: 20px;
        color: $light-2;
      }
      span {
        min-width: 15px;
        min-height: 15px;
        // margin: -1px 4px 0 0;
        cursor: pointer;
        border-radius: 50px;
        border: 1px solid $light-2;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0;

        .mask {
          // opacity: .2;
          transition: all 0.3s ease;
          min-height: 7px;
          min-width: 7px;
          border-radius: 50px;
          background: $light-2;
        }
      }
    }

    &:checked + label span {
      border: 1px solid $dark-2;
      .mask {
        background: $dark-2;
      }
    }
  }
}
