.add-fluxo {
  width: 380px;
  // position: relative;
  // top: -200px;

  .modal__title {
    margin-bottom: 10px;
  }

  .modal__content {
    .text {
      font-size: 12px;
      margin-top: 10px;
    }
    .content-1 {
      .content-img {
        display: flex;
        align-items: flex-end;
        margin-top: 15px;
        margin-bottom: 22px;

        .edit {
          position: relative;
          cursor: pointer;
          right: 20px;
          top: 7px;

          svg {
            width: 20px;
            height: 20px;
          }
          @include clickable();
        }
      }
      .img {
        width: 64px;
        height: 64px;
        border-radius: 50%;
        border: 1px solid #ccc;

        img {
          width: 64px;
          height: 64px;
          border-radius: 50%;
          object-fit: cover;
        }
      }
    }

    .content-2 {
      .folder {
        width: 265px;
        // height: 51px;
        padding: 19px 17px;
        font-size: 16px;
        color: $dark-2;
        border-radius: 4.5px;
        border: solid 1px #bcbfd0;
        background-color: #fff;
      }
    }
  }

  .modal__footer {
    display: flex;
    justify-content: flex-end;
    margin-top: 22px;
    .save {
      &--no-right {
        margin-right: 0;
      }
    }
  }
}
