@mixin ellipsis () {
    text-overflow: ellipsis;
    overflow: hidden; 
    white-space: nowrap;
    word-break: break-all;
}

@mixin clickable () {
    transition: .2s all;
    cursor: pointer;
    &:hover {
        opacity: 0.7;
    }

    &:active {
        opacity: 1.1;
    }
}

@mixin borderbottom {
    border-bottom: 1px solid $light-5;    
}
 