.headline {
    font-size: 30px;
    line-height: 36px;
    font-family: "Inter Bold"; 

    &--primary {
        color: $primary;
    }

    &--dark {
        color: $dark-1;
    }

    &--light {
        color: $light-2;
    }

}

.subHeadline {
    font-size: 20px;
    line-height: 22px;

    &--primary {
        color: $primary;
    }

    &--dark {
        color: $dark-1;
    }

    &--light {
        color: $light-2;
    }

}


.title {
    font-size: 18px;
    line-height: 22px;
    font-family: "Inter Bold";

    &--primary {
        color: $primary;
    }

    &--dark {
        color: $dark-1;
    }

    &--light {
        color: $light-2;
    }
}

.bodyText {
    font-size: 16px;
    line-height: 20px;
    // font-family: "Proxima Nova Regular";


    &--primary {
        color: $primary;
    }

    &--dark {
        color: $dark-1;
    }

    &--light {
        color: $light-2;
    }
}

.caption {
    font-size: 10px;
    line-height: 14px;
    font-family: "Inter Semibold";
    &--primary {
        color: $primary;
    }

    &--dark {
        color: $dark-1;
    }

    &--light {
        color: $light-2;
    }
}

.light {
    font-family: "Inter Light";
}

.regular {
    font-family: "Inter Regular";
}

.bold {
    font-family: "Inter Bold";
}

.semibold {
    font-family: "Inter Semibold";
}