@import "colors";
@import "typograthy";
@import "mixins";
* {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    outline: 0;
    text-decoration: none;
    list-style: none;
}

body,
html {
    position: relative;
    width: 100vw;
    max-width: 100vw;
    height: 100vh;
    padding: 0;
    margin: 0;
    overflow: hidden;
    font-family: "Inter Regular";
}
 
body,   
html { 
  position: relative; 
  width: 100vw; 
  max-width: 100vw; 
  height: 100vh;  
  padding: 0; 
  margin: 0;  
  overflow: hidden;  
  font-family: "Inter Regular";
}  
 
::-webkit-scrollbar { 
  width: 7px;
  height: 7px;
}

/* Track */
::-webkit-scrollbar-track {
  background: $light-3;
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: $light-2;
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: $dark;
}

