.chat {
  display: flex;
  flex-direction: column;
  height: 100%;

  .mini-chat__body {
    display: inline-flex;
    flex-direction: column;
    justify-content: flex-end;
  }

  .mini-chat__footer {
    padding: 14px 25px 14px 15px;

    &__btns {
      &:first-of-type {
        margin-left: 15px;
      }
    }
  }
}

.mini-chat {
  display: none;
  flex-direction: column;
  background: #fff;
  width: 300px;
  max-height: 500px;
  height: 70vh;
  border-radius: 17px;
  box-shadow: 0px 4px 20px #25265e1a;
  margin-bottom: 20px;
  z-index: 99;
  justify-content: flex-end;
  &__header {
    display: flex;
    border-bottom: 1px solid $light-1;
    padding: 13px 23px;
    &__user {
      display: flex;
      flex-grow: 1;
      align-items: center;
      &__img {
        width: 46px;
        height: 46px;
        border-radius: 50px;
        object-fit: cover;
        box-shadow: 0px 0px 10px -5px #bcbfd0;
      }
      &__info {
        margin-left: 14px;
        width: 56%;
        &__title {
          margin-bottom: 3px;
          font-size: 16px;
          line-height: 20px;
          font-family: "Inter Bold";
          @include ellipsis();
        }
        &__subtitle {
          font-size: 14px;
          line-height: 17px;
          font-family: "Inter Regular";
        }
      }
    }
  }
  &__body {
    height: calc(100% - 70px);
    position: relative;
    overflow-x: hidden;
    overflow-y: auto;
    &__welcome {
      width: 100%;
      height: 118px;
      background: $primary;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      &__img {
        width: 40px;
        height: 40px;
        border-radius: 50px;
        background: $primary-2;
        object-fit: cover;
        margin: 15px 0;
      }
      &__text {
        width: 243px;
        font-size: 14px;
        line-height: 15px;
        color: #fff;
        text-align: center;
      }
    }
  }
  &__message-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 15px;
    &__time {
      color: $dark-1;
      background-color: $light-3;
      text-align: center;
      padding: 5px 20px;
      border-radius: 50px;
      font-size: 14px;
      margin-bottom: 20px;
      margin-top: 10px;
    }
    &__img-bot {
      width: 32px;
      height: 32px;
      align-self: flex-end;
      border-radius: 50%;
      margin-top: 5px;
      object-fit: cover;
    }
    &__buddle {
      margin-bottom: 10px;
    
    &__call-bubble{
        display: flex;
        flex-direction: column;
        
    }
      &__text {
        font-size: 14px;
        word-wrap: break-word;
        padding: 10px 20px;
      }
      &__img {
        // margin-right: 18px;
        width: 32px;
        height: 32px;
        min-width: 32px;
        min-height: 32px;
        border-radius: 50%;
        align-self: flex-end;
        object-fit: cover;
        margin-right:18px; 
      }
     
      &--left {
        align-self: flex-start;
        display: flex;
        max-width: 80%;
        color: $dark-1;
        flex-direction:row;
        align-items: center;

        .mini-chat__message-container__buddle__call-bubble{     
            display: flex;
            flex-direction: column;
        
        }

        .mini-chat__message-container__buddle__text {
          border-radius: 20px 20px 20px 20px;
          margin-top: 5px;
          border: 1px solid $primary;
          &--left-last{
            border-radius: 20px 20px 20px 0px;
          }
        }
      }
      &--right {
        display: flex;
        flex-direction: column;
        align-self: flex-end;
        justify-content: flex-start;
        color: $light;
        max-width: 80%;
        .mini-chat__message-container__buddle__call-bubble{     
                display: flex;
                flex-direction: column;
            
        }
        .mini-chat__message-container__buddle__text {
          margin-top: 5px;
          background-color: $primary;
          border-radius: 20px 20px 20px 20px;

          &--last {
            border-radius: 20px 20px 0px 20px;
          }
        }
      }
    }
  }
  &__digitando {
    font-size: 12px;
    align-self: flex-end;
    color: #787993;
    background: #fff;
    width: 100%;
    margin: 20px
  }
  &__footer {
    display: flex;
    padding: 14px 15px;
    border-top: 1px solid $light-1;
    &__input-content {
      border: 1px solid $light-1;
      border-radius: 100px;
      position: relative;
      flex-grow: 1;
      &__input {
        width: 100%;
        padding: 0 50px 0 15px;
        height: 40px;
        background: transparent;
        border: none;
      }
      &__sent {
        position: absolute;
        right: 3px;
        bottom: 3px;
        border-radius: 50px;
        background: transparent;
        border: none;
        @include clickable();
      }
    }
    &__btns {
      border: none;
      border-radius: 50px;
      background: transparent;
      margin: 0 5px;
      @include clickable();
    }
  }

  &.active {
    display: flex;
  }
}
