.modal-fluxo {
    width: 581px;
  
    .name-fluxo {
      font-size: 14px;
      padding-top: 5px;
  
      input[type="text"] {
        font-family: "Inter Regular";
        padding: 17px 19px;
        width: 100%;
        border-radius: 4.5px;
        border: 1px solid $light-2;
        color: $light-2;
        &::placeholder{
          color: $light-2;
        }
      }
    }
  
    .select-fluxo {
      padding-top: 5px;
      font-size: 14px;
  
      select {
        font-family: "Inter Regular";
        padding: 17px 20px;
        border-radius: 4.5px;
        border: 1px solid $light-2;
        color: $light-2;
        cursor: pointer;
        width: 205px;
      }
  
      &--date {
        display: flex;
        justify-content: flex-start;
        color: $light-2;
  
        select {
          padding: 9px 13px;
          margin-right: 5px;
          width: auto;
          color: $light-2;
        
          
        }
      }
  
      .select-3 {
        margin-left: 15px;
        border: 1px solid $light-2;
        border-radius: 5px;
        cursor: pointer;
        text-align: center;
        align-self: center;
        padding: 9px 13px;
      }
    }
    .modal__close {
      color: $dark-3;
      font-weight: bolder;
      font-size: 18px;
    }
    .modal__title {
      color: $dark-3;
      padding-top: 5px;
    }
    .fluxo-title {
      font-size: 12px;
      padding-top: 5px;
    }
  
    .fluxo-text {
      font-size: 14px;
      padding-top: 5px;
    }
  
    .comunication-fluxo {
    }
  
    .days-container {
      display: flex;
      align-items: center;
      padding-top: 5px;
  
      &__days-item {
        text-align: center;
        line-height: 36px;
        cursor: pointer;
        filter: brightness(80%);
        width: 36px;
        height: 36px;
        border-radius: 50%;
        font-weight: bold;
  
        margin: 10px 10px 10px 0;
  
        &__check {
          background: $primary;
          color: #fff;
        }
        &:hover {
          background: $primary;
          color: #fff;
        }
        &:active {
          filter: brightness(100%);
        }
      }
    }
    .modal__footer {
      display: flex;
      justify-content: flex-end;
      align-items: flex-end;
      .modal__btn-primary {
        background: $primary;
        width: 97px;
        height: 40px;
        border-radius: 5px;
        font-size: 14px;
        @include clickable();
      }
    }
  }
  
  //Modal Fluxo End
  
  
  
  
  