.move-folder {
  width: 385px;
  height: 247px;
  padding: 20px;
  border-radius: 4px;
  box-shadow: 0 3px 11px 0 #20212626;
  // position: relative;
  // top: -200px;

  .modal__header {
  }
  .modal__title {
    font-weight: bold;
  }

  .content-1 {
    margin-bottom: 40px;
    .text {
      font-size: 12px;
      margin-bottom: 9px;
    }

    .provisorio {
      width: 265px;
      height: 51px;
      border-radius: 4.5px;
      border: solid 1px #bcbfd0;
    }
  }

  .modal__footer {
    display: flex;
    justify-content: flex-start;

    .move {
      width: 97px;
      height: 40px;
      border-radius: 5px;
      background-color: $primary;
    }
  }
}
