.search {
    display: flex;
    justify-content: space-between;
    border: 1px solid $light-2;
    border-radius: 5px;
    min-width: 190px; 
    
    &__input {
        width: calc(100% - 27px);
        line-height: 20px;
        padding: 8px 10px;  
        border-radius: 5px;
        border: none;
    } 

    &__btn { 
        align-self: center;
        margin: 3px 12px 0 0; 
        cursor: pointer;
    
        svg path { 
            fill: $light-2;
        }

        &:hover {
            svg path {
                fill: $primary-2;
            }
    
        }
        
        &:active {
            svg path {
                fill: $light-2;
            }    
        }
    }

    &--block {
        width: 100%;
    }
    &--fluxo{
        width: 320px;
        height: 40px;
        margin-right: 10px;
    }
}