.main {
  height: 100%;
  &__header {
    padding-bottom: 20px;
    display: flex;
    justify-content: space-between;

    &__title {
      display: flex;
      align-items: center;
      justify-content: space-between;

      &__back {
        border-radius: 5px;
        height: 40px;
        width: 40px;
        margin-right: 20px;
        background: $primary;
        display: flex;
        align-items: center;
        justify-content: center;
        color: white;
        padding-left: 5px;

        .material-icons {
          font-size: 16px;
        }
        @include clickable();
      }
    }

    &__btn {
      display: flex;
      justify-content: flex-end;

      &__item {
        &:first-child {
          margin-right: 10px;
        }

        &--fluxo {
          width: 320px;
          height: 40px;
          border-radius: 4.5px;
          border: solid 1px #bcbfd0;
        }
      }
    }
  }

  &__body {
    flex-direction: column;
    // align-items: center;
    justify-content: center;
    width: 100%;

    &__empty-state {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 70vh;

      &__img {
        animation: upAndDown 1s linear infinite;
        animation-duration: 3s;
        img,
        svg {
          width: 275px;
        }
      }

      &__btn {
        animation: upAndDown 1s linear infinite;
        animation-duration: 3s;
        margin-top: 20px;
      }
    }
  }

  .hidden {
    display: none;
  }

  .visible {
    display: grid;
  }
}

.first-fluxo {
  background: #f5f7ff;
  display: flex;
  position: relative;
  height: calc(100vh - 93px);
  justify-content: space-between;

  &__content-menu {
    display: flex;

    &__menu {
      display: none;
      align-items: center;
      width: 20px;
      justify-content: center;
      align-self: center;
      background-color: #fff;
      height: calc(100vh - 93px);
      border-right: 1px solid $light-5;
      cursor: pointer;

      &.active{
        display: flex;
      }

      &__items {
        &__item {
          width: 4px;
          height: 20px;
          border-radius: 3px;
          background: $light-5;
        }
      }
    }

    &__menu-open {
      display: none;
      width: 40px;
      background-color: #fff;
      display: none;
      height: calc(100vh - 93px);
      justify-content: center;
      border-right: 1px solid $light-5;

      &__items {
        &__item {
          padding-top: 16px;
          cursor: pointer;
          svg {
            path {
              fill: #8c8c9a;
            }
          }

          &.active,
          &:hover {
            svg {
              path {
                fill: $primary;
              }
            }
          }

          @include clickable();
        }
      }
      &.active {
        display: flex;
      }
    }

    
  }

  .title {
    font-family: "Inter Bold";
    font-size: 24px;
  }

  .container-blocks {
    position: relative;
    height: calc(100vh - 93px);
    display: none;
    font-family: "Inter Regular";

    &.active{
      display: flex;

    }

    .sub-title {
      font-size: 16px;
      margin-bottom: 10px;
      color: $dark-3;
    }
    .nav {
      width: 344px;
      height: calc(100vh - 93px);
      display: none;
      border-right: 1px solid $light-5;
      background: #fff;
      padding: 28px 22px;

      .header-block {
        display: flex;
      }

      &--block {
        padding: 0;

        .header-block {
          padding: 28px 22px;

          .title {
            font-family: "Inter Bold";
            font-size: 24px;
          }
        }

        .block-content {
          padding: 0 28px;
          display: flex;
          height: 56px;
          align-items: center;
          cursor: pointer;

          

          &:hover {
            background-color: $light-6;
          }

          width: 100%;
          &__icons {
            display: flex;
            &__icon {
              margin-right: 12px;
            }
          }
          &__text {
            font-size: 16px;
          }
        }

        .search {
          margin: 16px 22px;
        }
      }

      &--variable {
        padding: 0;

        .header-variable {
          padding: 28px 22px;
          @include borderbottom();
        }

        .variable__creating {
          padding: 27px 16px;
          @include borderbottom();

          &__area-input {
            display: flex;
            align-items: center;
            height: 40px;
            border: solid 1px $light-2;
            border-radius: 5px;
            padding: 0 14px;
            color: $dark-3;
            margin-bottom: 10px;
            &__icon {
              margin-right: 8px;
              margin-top: 3px;

              svg {
                width: 20px;
                height: 20px;
              }
            }
            &__input {
              font-family: "Inter Regular";
              border: none;
              font-weight: normal;
            }
          }

          &__text {
            font-size: 10px;
            color: $dark-3;
            span {
              font-weight: bold;
            }
          }
        }

        .variable__created {
          padding: 27px 16px;

          &__items {
            display: grid;
            grid-template-columns: repeat(auto-fit, minmax(133px, 0.5fr));
            grid-gap: 10px;
            color: $dark-2;
            &__item {
              display: flex;
              align-items: center;
              min-width: 133px;
              height: 40px;
              border-radius: 5px;
              border: solid 1px #e2e2ec;
              background-color: #fbfbff;
              font-size: 14px;
              word-break: break-all;

              cursor: pointer;

              i {
                font-size: 18px;
                margin-top: 2px;
                margin-right: 8px;
                margin-left: 14px;

                @include clickable();
              }
              span {
                color: $primary;
              }
            }
          }
        }
      }

      &--tree {
      }

      &--linear {
      }

      &.active {
        display: block;
      }
    }

    .search {
      margin: 16px 0;
    }

    .opener {
      align-self: center;
      margin-left: 250px;
      width: 4px;
      height: 20px;
      border-radius: 3px;
      background: $light-5;
      cursor: pointer;
      position: absolute;
      right: -10px;
    }
  }

  &__container-start {
    border-left: 1px solid $light-5;
    height: calc(100vh - 109px);
    width: 369px;
    background-color: #fff;
    z-index: 1;
    font-family: "Inter Regular";
    display: none;
    &.active{
    display: block;

    }

    &__header {
      display: flex;
      justify-content: space-between;
      padding: 28px 16px 26px 16px;
      @include borderbottom();

      &__actions {
        display: flex;
        .icon {
          margin-left: 19px;
        }
      }
    }
    &__main {
      padding: 21px 16px 21px 16px;
      @include borderbottom();

      &__textarea {
        font-family: "Inter Regular";
        font-size: 14px;
        line-height: 19px;
        width: 100%;
        height: 68px;
        border-radius: 4px;
        border: solid 1px #bcbfd0;
        resize: none;
        padding: 10px 12px;
        color: $dark-4;
      }

      &__text {
        font-family: "Inter bold";
        font-size: 10px;
        letter-spacing: 0;
        margin: 3px 0 0 0;
      }
    }

    &__content {
      padding: 18px 16px 24px 16px;
      @include borderbottom();

      &__header {
        display: flex;
        justify-content: space-between;
        align-items: center;

        &__text {
          font-size: 12px;
          color: $dark-2;
        }
      }

      &__main {
        display: flex;
        justify-content: center;

        .btn-add-answer {
          font-family: "Inter Regular";
          display: flex;
          align-items: center;
          padding: 0 14px;
          color: $dark-2;
          font-size: 14px;
          width: 191px;
          height: 40px;
          border-radius: 5px;
          border: solid 1px #e2e2ec;
          background-color: #fbfbff;
          margin-top: 12px;
          @include clickable();

          svg {
            margin-right: 8px;
            width: 20px;
            height: 20px;
          }
        }
      }
      &--object {
        border-bottom: 0;

        .first-fluxo__container-start__content__main {
          justify-content: flex-start;
        }

        .btn-add-object {

          font-family: "Inter Regular";
          display: flex;
          align-items: center;
          border: 1px dashed #d9dae1;
          background: #fff;
          height: 40px;
          font-size: 14px;
          border-radius: 5px;
          padding: 0px 14px;
          cursor: pointer;
          color: $dark-2;
          margin-top: 16px;
          i {
            font-size: 18px;
            margin-top: 2px;
          }
        }
      }
    }

    .icon {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 26px;
      height: 26px;
      border-radius: 50%;
      margin-left: 20px;
      border: solid 1px $light-5;
      @include clickable();
      object-fit: contain;

      svg {
        width: 20px;
        height: 20px;
      }

      &--icon-small {
        width: 19px;
        height: 19px;
        svg {
          width: 15px;
          height: 15px;
        }
      }
    }
  }

  &__add-box {
    width: 44px;
    height: 44px;
    border-radius: 50%;
    border: 2px solid $primary;
    position: absolute;
    right: 37px;
    top: 37px;
    background: #fff;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    @include clickable();

    svg {
      width: 24px;
      path {
        fill: $primary;
      }
    }
  }

  .fluxo-btn-action {
    align-self: flex-end;
    display: flex;
    position: absolute;
    bottom: 20px;

    left: calc(50% - 118px);

    svg {
      path {
      }
    }

    &--right {
      margin-right: 10px;
      position: initial;

      .view {
        margin-left: 27px;
      }

      .notes {
        margin-left: 27px;
      }

      button {
        box-shadow: 0 3px 11px 0 rgba(32, 33, 38, 0.15);
      }
    }

    &--left {
      margin-right: 10px;
      position: initial;

      .zoom-in {
        border-right: 0;
        border-radius: 5px 0 0 5px;
      }
      .zoom-out {
        border-left: 0;
        border-radius: 0 5px 5px 0;
        &:hover{
          svg{
            path{
              color: $primary;
            }
          }
        }
      }

      box-shadow: 0 3px 11px 0 rgba(32, 33, 38, 0.15);
    }

    button {
      width: 40px;
      height: 40px;
      padding: 8px;
      
    }
  }
}

@keyframes upAndDown {
  0%,
  100% {
    margin-bottom: 20px;
  }
  50% {
    margin-bottom: 0px;
  }
}
