.flex {
    display: block;
    width: 100%;
    &--full-height {
        height: 100%;
        .flex {
            &__container,
            &__row {
                height: 100%;
            }
        }
    }
    &__container {
        display: block;
        padding:20px 50px;
        box-sizing: border-box;
        margin: 0 auto;
        
        &--no-padding {
            padding: 0;
        }
    }
    &__row {
        display: flex;
        flex-direction: row;
        &--dash-live{
            flex-direction: column;
        }
        justify-content: space-between;
    }
    &__cell {
        width: auto;
        box-sizing: border-box;
        // margin: 12px;
        // background: $color-accent-dark-2;
        &--1 {
            
        }
        &--2 {
            
        }
        &--3 {
            
        }
        &--4 {
            width: 242px;
            margin-right: 18px;
        }
        &--5 {
            
        }
        &--6 {
            
        }
        &--7 {
            
        }
        &--8 {
            width: calc(100% - 325px);
        }
        &--9 {
            
        }
        &--10 {
            
        }
        &--11 {
            
        }
        &--12 {
           
        }
        &--align-center {
            align-self: center;
        }
    }
    &--full-width {
        .flex {
            &__container {
                max-width: 100%;
                padding: 0;
            }
        }
    }
}

.flex--flow {}

#drop-options-modais{
    font-family: 'Inter SemiBold';
    .options-change-action{
        padding: 15px 18px;
        &__item{
            padding-bottom: 22px;
            cursor:pointer;
            font-size: 14px;
            display: flex;
            align-items: center;
            @include clickable();

            &:first-child{
                color: $accent-3;
            }
            &:last-child{
                padding-bottom: 0;
            }

            svg{
                margin-right: 14px;
            }
        }
    }
}