@import "include-media";

.flex-row {
  display: flex;
  justify-content: space-between;
}

.flex-column {
  display: flex;
  flex-direction: column;
}

.dash-performance {
  display: flex;

  .card-performance {
    background: #fff 0% 0% no-repeat padding-box;
    box-shadow: 0px 4px 20px #25265e1a;
    margin-bottom: 30px;
    padding: 30px 36px 6px 36px;
    border-radius: 17px;
    width: 100%;

    &__header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 22px;

      &__title {
        font-size: 16px;
        line-height: 20px;
        color: $light-2;
      }

      &__date-picker {
      }
    }

    &__chart {
    }
  }

  .resume-performace {
    display: flex;
    flex-direction: column;
    height: 426px;

    .card-resume {
      margin-left: 28px;
      margin-right: 0;
      text-align: center;
      max-width: 100%;
      justify-content: center;
      height: 100%;

      &:last-of-type {
        margin-bottom: 0;
      }
    }
  }
}

.path {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;

  &__item {
    display: flex;
    font-family: "Inter Bold";
    cursor: pointer;
    &__text {
      font-size: 14px;
      line-height: 20px;
      color: $light-1;
    }
    &__icon {
      margin: 0 5px 0 0px;
      .material-icons {
        font-size: 20px;
        color: $light-1;
      }
    }

    &:last-child {
      & .path__item__text {
        font-size: 14px;
        line-height: 20px;
        text-transform: uppercase;
        text-decoration: underline;
        color: $dark;
      }
      & .path__item__icon {
        display: none;
      }
    }

    &:active {
      filter: brightness(0.8);
    }
  }
}

.inside-menu {
  display: flex;
  margin-top: 10px;

  &__item {
    font-size: 14px;
    line-height: 17px;
    padding: 8px 0;
    margin: 0 17px;
    text-transform: uppercase;
    transition: 0.2s all;
    cursor: pointer;
    color: $light-2;
    font-family: "Inter Regular";

    &:hover {
      color: $primary;
      font-family: "Inter Bold";
      border-bottom: 3px solid $primary;
      filter: opacity(90%);
    }

    &.active {
      color: $primary;
      font-family: "Inter Bold";
      border-bottom: 3px solid $primary;
    }
    &:first-child {
      margin-left: 22px;
    }
  }
}

.btn-group-row {
  margin: 20px 0 32px 0;
}

.radio-group-row {
  margin-bottom: 20px;
}

.table-card {
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 4px 20px #25265e1a;
  border-radius: 17px;
  padding: 0 24px 36px 36px;
  font-size: 16px;
  line-height: 20px;
  cursor: pointer;
  font-family: "Inter Regular";

  overflow: auto;

  &::-webkit-scrollbar {
    // width: 0px;
  }

  &__header {
    display: flex;
    padding: 22px 0 0 0;
    padding-left: 8px;
    // border-bottom: 1px solid $light-3;
    text-transform: uppercase;
    color: $light-2;
    // align-items: center;
    justify-content: flex-start;

    & .cres {
      &::after {
        content: "\2B9D";

        margin-left: 3px;
        visibility: visible;
        font-size: 12px;
      }
    }

    & .dec {
      &::after {
        content: "\2B9D";

        margin-left: 3px;
        visibility: visible;
        transform: rotate(180deg);
        font-size: 12px;
      }
    }

    .table-card__item {
      justify-content: flex-start;
      padding-right: 8px;
      padding-left: 15px;
      display: flex;
      align-items: center;
      padding-bottom: 21px;
      border-bottom: 1px solid $light-3;
      &:last-child {
        padding-right: 8px;
      }

      &-1 {
        width: 280px;

        min-width: 280px;
      }
      &-2 {
        min-width: 120px;
        padding-left: 10px;
      }
      &-3 {
        min-width: 120px;
      }
      &-4 {
        min-width: 120px;
      }
      &-5 {
        min-width: 180px;
      }
      &-6 {
        min-width: 120px;
      }
      &-7 {
        min-width: 120px;
      }
      &-8 {
        min-width: 120px;
      }
      &-9 {
        max-width: 180px;
        min-width: 120px;
      }
      &-10 {
        min-width: 120px;
      }

      .user-info {
        width: 100%;
      }
    }
  }

  &__body {
    display: flex;
    flex-direction: column;
    color: $dark;
    height: 50vh;

    &__row {
      color: $dark;
      display: flex;
      align-items: center;
      margin-bottom: 17px;
      transition: 0.2s all;
      padding: 10px 0;
      padding-left: 8px;

      &:hover {
        background: $light;
        cursor: pointer;
      }

      &:first-child {
        margin-top: 28px;
      }

      &:last-child {
        margin-bottom: 0px;
      }
    }
  }

  &__item {
    justify-content: flex-start;
    padding-right: 8px;
    padding-left: 15px;
    display: flex;
    align-items: center;

    &-1 {
      width: 280px;

      min-width: 280px;
    }
    &-2 {
      min-width: 120px;
      padding-left: 10px;
    }
    &-3 {
      min-width: 120px;
    }
    &-4 {
      min-width: 120px;
    }
    &-5 {
      min-width: 180px;
    }
    &-6 {
      min-width: 120px;
    }
    &-7 {
      min-width: 120px;
    }
    &-8 {
      min-width: 120px;
    }
    &-9 {
      max-width: 180px;
      min-width: 120px;
    }
    &-10 {
      min-width: 120px;
    }

    .user-info {
      width: 100%;
    }
  }

  &--flows-in {
    display: none;
    .table-card__item {
      // margin-left: 5px;

      &:first-child {
        margin: 0;
      }

      &-1 {
        min-width: 280px;
      }
      &-2 {
        min-width: 158px;
        padding-left: 10px;
      }
      &-3 {
        min-width: 158px;
      }
      &-4 {
        min-width: 180px;
      }
      &-5 {
        min-width: 120px;
      }
      &-6 {
        min-width: 120px;
      }
      &-7 {
        min-width: 120px;
      }
      &-8 {
        min-width: 120px;
      }
      &-9 {
        min-width: 176px;
      }
      &-10 {
        min-width: 120px;
      }
    }

    &.active {
      display: block;
    }
  }

  &--flows-out {
    display: none;
    .table-card__item {
      &-1 {
        min-width: 280px;
      }
      &-2 {
        min-width: 158px;
        padding-left: 10px;
      }
      &-3 {
        min-width: 184px;
      }
      &-4 {
        min-width: 152px;
      }
      &-5 {
        min-width: 180px;
      }
      &-6 {
        min-width: 120px;
      }
      &-7 {
        min-width: 120px;
      }
      &-8 {
        min-width: 120px;
      }
      &-9 {
        min-width: 256px;
        min-width: 120px;
      }
      &-10 {
        min-width: 120px;
      }
    }

    &.active {
      display: block;
    }
  }
}

.client-details-path {
  margin-left: 60px;
}

.client-details-container {
  display: flex;
  margin-top: 25px;
}

.client-details {
  min-height: 507px;
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 4px 20px #25265e1a;
  border-radius: 17px 0px 0px 17px;
  padding: 36px 24px 10px 36px;
  width: 50%;

  &__info {
    width: 100%;

    &__header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 30px;
    }

    &__resume {
      display: flex;
      align-items: flex-start;
      font-family: "Inter Regular";
      font-size: 16px;
      line-height: 20px;

      &-1 {
        width: 195px;
      }
      &-2 {
        flex-grow: 1;
      }
      &__row {
        display: flex;
        margin-bottom: 13px;

        &:last-child {
          margin-bottom: 0;
        }
      }

      &__item {
        display: flex;
        align-items: center;
        margin-bottom: 13px;
      }

      &__text {
        color: $light-2;
        margin-right: 10px;
      }

      &__description {
        color: $dark;

        .media .media__text {
          font-size: 16px;
          line-height: 20px;
          color: $dark;
        }
      }
    }
  }

  &__interactions {
    margin-top: 61px;

    &__container {
      overflow-y: scroll;
      height: 200px;
    }

    &__title {
      font-size: 16px;
      line-height: 20px;
      font-family: "Inter Bold";
      color: $dark;
    }

    &__body {
      &__row {
        display: flex;
        align-items: center;
        border-bottom: 1px solid $light-3;
        padding: 15px 0;
      }

      &__item {
        &-1 {
          width: 173px;
        }
        &-2 {
          width: 80px;
        }
        &-3 {
          width: 118px;
        }
        &-4 {
          width: 118px;
        }

        &__title {
          font-size: 16px;
          line-height: 20px;
          color: $dark;
        }

        &__text {
          font-size: 14px;
          line-height: 17px;
          color: $light-2;
        }

        &__icon {
          svg {
            width: 15px;
            height: 15px;
          }
        }

        .media__icon {
          svg {
            width: 20px;
            height: 20px;
          }
        }
      }
    }
  }

  .media {
    display: flex;
    align-items: center;

    &__icon {
      display: flex;
      align-items: center;

      margin-right: 10px;
      svg {
        width: 15px;
        height: 15px;
      }
    }
    &__text {
      font-size: 14px;
      line-height: 17px;
      color: $light-2;
    }
  }
}

.chat-content {
  min-height: 507px;
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 4px 20px #25265e1a;
  border-radius: 0px 17px 17px 0px;
  width: 50%;
}

.dash-resume-content {
  display: flex;
  flex-wrap: wrap;
  height: 100%;
  width: 100%;
}

.card-resume {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: calc(50% - 28px);
  width: 385px;
  height: 185px;
  border-radius: 17px;
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 4px 20px #25265e1a;
  margin: 0 28px 30px 0;
  position: relative;
  flex-shrink: initial;
  flex-grow: 1;
  justify-content: flex-end;

  // Css dos graficos
  .chartLine,
  .chartColumn {
    width: 100%;
    border-radius: 60px;
    .apexcharts-legend {
      border-bottom-left-radius: 555px;
      border-bottom-right-radius: 5px;
    }
  }

  .chartBar {
    width: 60%;
  }

  &__content {
    position: absolute;
    top: 10px;
    left: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    &__title {
      font-size: 30px;
      line-height: 39px;
      color: $primary;
      font-family: "Inter Bold";
    }

    &__text {
      font-size: 16px;
      line-height: 20px;
      color: $light-2;
    }

    &__icon {
      width: 39px;
      height: 39px;
      margin-bottom: 18px;

      svg path {
        fill: $primary;
      }
    }

    &:first-child {
    }

    &:last-child {
    }
  }

  &__option {
    position: absolute;
    top: 15px;
    left: 15px;
    font-size: 20px;
    color: $light-2;
    transition: 0.2s;
    cursor: pointer;
    opacity: 0;

    .none {
      display: none;
    }

    .opt {
      &:hover {
        filter: brightness(80%);
      }

      &:active {
        filter: brightness(100%);
      }
    }

    &__items {
      font-size: 16px;
      opacity: 1;
      background: #fff;
      z-index: 2;
      color: $dark;
      padding: 20px;
      width: 185px;
      &__item {
        display: flex;
        flex-direction: row;
        align-items: center;
        cursor: pointer;
        font-size: 16px;
        padding: 5px;
        svg {
          margin-right: 10px;
          width: 14px;
          height: 14px;
        }


        @include clickable();
      }
    }
  }

  &__close {
    position: absolute;
    top: 15px;
    right: 15px;
    font-size: 20px;
    color: $light-2;
    transition: 0.2s;
    cursor: pointer;
    opacity: 0;

    &:hover {
      filter: brightness(80%);
    }

    &:active {
      filter: brightness(100%);
    }
  }

  &--add {
    text-align: center;

    .card-resume__content {
      position: initial;
      .card-resume__content__text {
        color: $primary;
      }
    }

    justify-content: center;

    cursor: pointer;

    @include clickable();
  }

  &:hover {
    .card-resume__close {
      opacity: 1;
    }

    .card-resume__option {
      opacity: 1;
    }
  }

  // &--first {
  //   flex-grow: 1;
  // }
  // &--last {
  //   margin-bottom: 0;
  //   flex-grow: 1;
  //   align-self: flex-end;
  // }
}
@media (max-width: 1920px) {
  .card-resume {
    width: 415px;
    flex-grow: 0;
  }
}

.dash-topfive-content {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.top-five {
  width: 380px;
  height: 400px;
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 0px 4px 20px #25265e1a;
  padding: 40px 5px 40px 36px;
  border-radius: 17px;
  margin-bottom: 30px;

  &__title {
    text-transform: uppercase;
    color: $light-2;
    font-size: 16px;
    line-height: 20px;
    margin-bottom: 22px;
    font-family: "Inter Regular";
  }
  &__content {
    width: 100%;
    height: 100%;
    // overflow-y: auto;
    overflow-x: hidden;
    position: relative;

    &__item {
      display: flex;
      align-items: center;
      width: 100%;
      padding: 5px 0 0 5px;
      margin-bottom: 9px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    &__btn {
      position: absolute;
      right: 30px;
      bottom: 37px;
    }
  }
}
@media (max-width: 1920px) {
  .top-five {
    width: 413px;
  }
}

.user-info {
  display: flex;
  align-items: center;

  span {
    svg {
      width: 16px;
      height: 16px;
    }
  }

  .socials-icons {
    align-self: flex-start;
    margin: 5px 10px 0 5px;
  }

  &__photo {
    width: 46px;
    height: 46px;
    border-radius: 50%;
    box-shadow: 0px 0px 10px -5px $dark-1;
    margin-right: 8px;

    &__img {
      width: 46px;
      height: 46px;
      border-radius: 50px;
      object-fit: cover;
    }
  }

  &__text {
    display: flex;
    flex-direction: column;
    width: 100%;

    &__title {
      color: $dark;
      min-width: 240px;
      font-size: 16px;
      line-height: 20px;
      font-family: "Inter Bold";
      display: flex;
      align-items: center;
      @include ellipsis();

      &__name {
        @include ellipsis();
        max-width: 250px;
      }
    }

    &__subtitle {
      color: $light-2;
      max-width: 160px;
      min-width: 140px;
      font-size: 16px;
      line-height: 20px;
      font-family: "Inter Regular";
      @include ellipsis();
    }
  }
}

.top-five {
  .user-info__text {
    width: 250px;
    &__title {
      max-width: 100%;
    }
  }
}

.table-card__body {
  .user-info__text {
    width: 180px;
    &__title,
    &__title__name {
      max-width: 100%;
      min-width: unset;
    }
  }
}

.table-card--flows-out {
  .user-info__text {
    width: 100px;
    &__title,
    &__title__name {
      max-width: 100%;
      min-width: unset;
    }
  }
}
