.folders {
  &__content {
    &__item {
      display: flex;
      align-items: center;
      width: 100%;
      cursor: pointer;
      height: 44px;
      align-items: center;
      justify-content: space-between;
      font-size: 14px;

      &__identify {
        display: flex;
        justify-content: center;
        align-items: center;

        &__name {
          font-size: 14px;

          
        }
        .img {
          width: 18px;
          height: 18px;
          margin-right: 17px;
          margin-left: 13px;

        }
      }

      .open-tippy {
        opacity: 0;
        justify-self: flex-end;
        margin-right: 10px;
      }

      &__icon {
        width: 20px;
        margin-right: 10px;

        svg path {
          fill: $light-2;
        }
      }

      &__title {
        color: $dark;
      }

      &:first-child {
        margin-top: 14px;
      }

      &:hover {
        background: $light-4;

        .open-tippy {
          opacity: 1;

          .material-icons {
            font-size: 20px;
            padding: 4px 4px;
            color: $primary;
          }
          &:hover {
            text-align: center;
            box-shadow: 0 3px 11px 0 #20212626;
            border-radius: 50%;
          }
        }

        .folders__content__item {
          &__title {
            opacity: 0.7;
          }
        }
      }

      &:active {
        .folders__content__item {
          &__title {
            opacity: 1;
          }
        }
      }

      &.active {
        .folders__content__item {
          &__icon {
            svg path {
              fill: $primary;
            }
          }

          &__title {
            color: $primary;
            font-family: "Inter Bold";
            font-weight: 700;
          }
        }
      }
    }
  }

  &__new {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    border: 1px dashed $light-1;
    border-radius: 5px;
    transition: 0.2s;
    cursor: pointer;

    &:hover {
      background: $light;
    }

    &:active {
      background: $light-1;
    }

    &__item {
      display: flex;
      align-items: center;
      margin: 16px;

      &__icon {
        width: 20px;
        margin-right: 5px;

        svg path {
          fill: $dark;
        }
      }

      &__title {
        color: $dark;
      }
    }
  }
}

.path-title {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 7px 0 12px 0;
  font-size: 16px;
  font-weight: bold;

  &__icon {
    width: 16px;
    margin-right: 7px;
    svg {
      width: 16px;
      height: 16px;
      fill: $light-2;
    }
    // svg path {

    // }

    @include clickable();
    &:hover {
      background: $light;
    }
  }

  &__title {
    color: $dark-3;
  }
  border-bottom: 2px solid $light-2;
  padding: 5px 0;
}

.flow-list {
  &__header {
    color: $light-2;
    font-family: "Inter Semibold";
    letter-spacing: 2px;
    text-transform: uppercase;
    font-size: 12px;
    
    .flow-list__item {
      border: none;
      &:hover{
        box-shadow: none;
      }
    }
  }

  &__body {
    color: $dark;
    font-size: 14px;
    line-height: 17px;
    font-family: "Inter Regular";
    height: 50vh;
    overflow-y: auto;
    padding-left: 6px;
    padding-right: 6px;
    padding-bottom: 15px;

    
  }

  &__item {
    display: flex;
    align-items: center;
    border-top: 1px solid $light-3;
    padding: 19px 18px;
    height: 55px;
    cursor: pointer;


    &__status {
      display: flex;
      align-items: center;
      width: 16px;
      margin-right: 15px;
      margin-left: 5px;

      &__item {
        width: 8px;
        height: 8px;
        border-radius: 50px;

        &.enable {
          background: $accent;
        }

        &.disable {
          background: $accent-2;
        }
      }
    }

    &__name {
      align-items: flex-start;

      flex-grow: 3;
      width: 100%;
      max-width: 250px;
      padding-right: 10px;
      @include ellipsis();
    }

    &__date {
      min-width: 150px;
      padding-right: 10px;
      @include ellipsis();
    }

    &__canal {
      min-width: 150px;
      padding-right: 10px;
      @include ellipsis();
    }

    &__creating {
      min-width: 150px;
      @include ellipsis();
    }

    &__action {
      display: flex;
      justify-content: center;
      flex-grow: 1;
      max-width: 100px;
      @include ellipsis();

      &__item {
        display: flex;
        align-items: flex-start;
        transition: 0.2s all;
        opacity: 0;
        cursor: pointer;

        .material-icons {
          font-size: 20px;
          padding: 4px 4px;
          color: $primary;
          &:hover {
            text-align: center;
            box-shadow: 0 3px 11px 0 #20212626;
            border-radius: 50%;
            background: $light-4;
          }
        }
      }
    }

    &:hover {
      box-shadow: 0 3px 11px 0 #20212626;
      
      padding: 19px 18px;
      height: 55px;
      
      border-radius: 4px;

      .flow-list__item__action__item {
        opacity: 1;

        &:active {
          opacity: 0.8;
        }
      }
    }
  }
}


.separator-btn{
  position: relative;
  top:10px;
  left: 42px;
  height: 20px;
  border-radius: 5px;
  border: solid 1px #d9dae1;
  z-index: 1;
}
.separator-header{
  position: relative;
  top: 0;
  left: 20px;
  width: 2px;
  height: 20px;
  border: solid 1px #e2e2ec;
  z-index: 1;
}

.choose-channels {
  display: flex;
  width: 100%;

  &__custom-bot {
    display: flex;
    flex-direction: column;
    &__name {
      margin-bottom: 25px;

      .btn--light {
        width: 300px;
        border-radius: 5px;
        padding: 8px 14px;
        font-size: 14px;
        color: $dark-1;
      }
    }

    &__title {
      color: $light-2;
      font-size: 16px;
      padding-bottom: 10px;
    }
  }

  &__button-group {
    width: calc(50% - 80px);
    margin: 36px 20px 0 60px;
  }
  &__info {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 60px 0 40px 0;
    z-index: 0;
    justify-content: center;
    width: 50%;
    margin-top: -61px;
    box-shadow: -10px 0px 10px -8px #ddd;
    -webkit-box-shadow: -10px 0px 10px -8px #ddd;

    &__img {
      svg {
        width: 86px;
        height: 105px;
      }
    }

    &__content {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 36px;

      &__title {
        margin-bottom: 10px;
      }

      &__text {
        width: 300px;
        text-align: center;
        .bodyText {
          margin-bottom: 10px;
        }
      }
    }
  }
}
