/**************************\
  Basic Modal Styles
\**************************/

.modal {
  font-family: "Inter Regular", sans-serif;

  &__over {
    position: absolute;
    top: 0;
    width: 100vw;
    height: 100vh;
    z-index: 3;
  }

  &__overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 4;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.2);
  }
  &__container {
    background-color: #fff;
    padding: 22px;
    max-height: 90vh;
    border-radius: 4px;
    overflow-y: auto;
    box-sizing: border-box;
    border-radius: 4px;
    box-shadow: 0 3px 11px 0 #20212626;
    border: 1px solid $light;
    z-index: 3;
  }
  &__header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    font-family: "Inter SemiBold", sans-serif;
    letter-spacing: normal;
    font-weight: bold;
  }

  &__title {
    margin: 0;
    font-size: 20px;
    margin: 4px 0 18px 0px;
    color: #3e3f44;
    font-weight: 700;
  }

  &__close {
    background: transparent;
    border: 0;
    cursor: pointer;
    padding: 3px;
    font-size: 16px;
    font-weight: bold;
  }

  .modal__header .modal__close:before {
    content: "\2715";
    cursor: pointer;
  }

  &__btn {
    font-family: "Inter Regular", sans-serif;
    font-weight: bold;
    font-size: 0.875rem;
    padding-left: 1rem;
    padding-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: #e6e6e6;
    color: rgba(0, 0, 0, 0.8);
    border-radius: 0.25rem;
    border-style: none;
    border-width: 0;
    cursor: pointer;
    -webkit-appearance: button;
    text-transform: none;
    overflow: visible;
    line-height: 1.15;
    margin: 0;
    will-change: transform;
    -moz-osx-font-smoothing: grayscale;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    transition: -webkit-transform 0.25s ease-out;
    transition: transform 0.25s ease-out;
    transition: transform 0.25s ease-out, -webkit-transform 0.25s ease-out;
    @include clickable();
  }

  &__content {
    line-height: 1.5;
    color: rgba(0, 0, 0, 0.8);
  }

  &__btn-primary {
    color: #fff;
    margin-right: 25px;
    width: 97px;
    height: 40px;
    border-radius: 5px;
    background-color: $primary;
  }
}

