.card {
    padding: 22px 28px;
    border-radius: 17px;
    background: #FFF;
    -webkit-box-shadow: 0px 4px 20px 0px $light-2;
    -moz-box-shadow: 0px 4px 20px 0px $light-2;
    box-shadow: 0px 4px 20px 0px $light-2;        

    &--expand {        

        &__header {
            display: flex; 
            align-items: center;
            justify-content: space-between;
            height: 40px;            
            cursor: pointer;    
        }

        &__body {
            display: none;
        }
 
        &__title {                    
            display: flex;
            align-items: center;

            &__step {                            
                display: flex;
                align-items: center;
                justify-content: center;
                width: 34px;
                height: 34px;            
                border-radius: 50px;
                margin-right: 22px;
                background: $primary;
                color: #FFF;
                font-size: 18px;
                line-height: 22px;
                font-family: "Inter Bold";
                
                &--done {
                    background: $accent;
                }

                .material-icons {                 
                }
            }

            &__text {

            }
        }

        &__btn {  
            .btn--primary { 
                display: none;
            }
        }

        &--expanded {
            position: relative;
            padding-bottom: 0; 

            &::before {  
                content: '';
                width: 12px;
                margin-top: -22px;
                height: 100%;
                background: $primary;
                border-radius: 17px 0 0 17px / 40px 0 0 40px;                            
                position: absolute;
                left: 0;    
            }
            .card--expand__body {
                display: block;
                
            }
            .card--expand__btn { 
                .btn-text {
                    display: none;
                }

                .btn--primary { 
                    display: block; 
                }
            }   
        }  
    }
     
    &--fluxo { 
        min-height: 420px;
        // height: 68vh;
        border: none;
        padding: 0;
        box-shadow: none;
    }
}

