.login {
    background: white;
    width: 100%;
    height: 100%;
    
    &__wrapper {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);        
        background: white;
        border-radius: 15px;
        background: #FFFFFF 0% 0% no-repeat padding-box;
        box-shadow: 0px 4px 20px #25265E1A;
        padding: 30px 197px 42px 197px;


    } 

    &__content{        
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: 324px;
        height: auto;
         
        &__logo { 
            width: 100%;
            &__img {            
                position: relative;
                width: 161px;
                
                img {
                    position: absolute;
                    top: -70px;
                    right: 25px;
                    width: 100%;
                    object-fit: cover;
                }
            }
            &__text {
                width: 100%;
                text-align: right;
                font-size: 61px;
                line-height: 74px;
                font-family: "Inter Bold";
                color: $primary;
            }
        }

        &__title {
            font-size: 20px;
            line-height: 24px;
            width: 100%;
            text-align: center;
            color: $primary;
            font-family: "Inter Bold";
            margin: 56px  0 45px;
        }

        &__form {
            padding: 0 17px;
            &__input {
                display: flex;
                flex-direction: column;
                font-size: 16px;
                line-height: 20px;
                margin-bottom: 15px;

                & label {
                    color: $dark;
                    margin-bottom: 10px;
                }

                & input {
                    padding: 9px 10px;
                    border-radius: 5px;
                    border: 1px solid $light-2;
                    font-size: 16px;
                    line-height: 20px;
                }
            } 
            &__terms {
                display: flex;
                align-items: center;
                color: $light-2;
                font-size: 16px;
                line-height: 20px;
                font-family: "Inter Regular";
                margin: 11px 0 37px 0;
                &__text { 
                    padding-left: 8px;
                }

                &__link {
                    color: $primary-4;
                    text-decoration: none;                    
                    cursor: pointer;
                    margin-left: 1px;
                    &:active {
                        opacity: 0.8;
                    }
                }
            }
            &__btn-container { 
                display: flex;
                  
                .btn {
                    width: 100%;

                    &:first-child {
                        margin-right: 28px;
                    }
                }
                .btn-text {
                    width: 100%;
                    text-align: right;
                    padding-right: 0;
                    
                }

            }
            
        }    
    }
}