@import 'colors';

.tippy-popper[x-placement^=top] .tippy-tooltip.genium-theme .tippy-arrow {
  border-top: 8px solid #fff;
  border-right: 8px solid transparent;
  border-left: 8px solid transparent
}

.tippy-popper[x-placement^=bottom] .tippy-tooltip.genium-theme .tippy-arrow {
  border-bottom: 8px solid #fff;
  border-right: 8px solid transparent;
  border-left: 8px solid transparent
}

.tippy-popper[x-placement^=left] .tippy-tooltip.genium-theme .tippy-arrow {
  border-left: 8px solid #fff;
  border-top: 8px solid transparent;
  border-bottom: 8px solid transparent
}

.tippy-popper[x-placement^=right] .tippy-tooltip.genium-theme .tippy-arrow {
  border-right: 8px solid #fff;
  border-top: 8px solid transparent;
  border-bottom: 8px solid transparent
}

.tippy-tooltip.genium-theme {
  color: #26323d;
  -webkit-box-shadow: 0px 0px 8px #51525A29;
          box-shadow: 0px 0px 8px #51525A29;
  background-color: #fff;
  padding: 0;
}

.tippy-tooltip.genium-theme .tippy-backdrop {
  background-color: #fff
}

.tippy-tooltip.genium-theme .tippy-roundarrow {
  fill: #fff
}

.tippy-tooltip.genium-theme[data-animatefill] {
  background-color: transparent
}

.tippy-tooltip {
  .tippy-content {
    font-size: 12px;
  }
}